"use client";

import { useEffect, useRef } from "react";

export default function CameraTestPage() {
  const videoRef = useRef<HTMLVideoElement>(null);

  useEffect(() => {
    async function enableCamera() {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({
          video: { facingMode: "user" }, // "environment" „Å´Â§â„Åà„Çã„Å®„Ç¢„Ç¶„Éà„Ç´„É°„É©
        });

        if (videoRef.current) {
          videoRef.current.srcObject = stream;
        }
      } catch (err) {
        console.error("üì∑ „Ç´„É°„É©„ÅÆ‰ΩøÁî®„Å´Â§±Êïó„Åó„Åæ„Åó„Åü:", err);
        alert("„Ç´„É°„É©„Å∏„ÅÆ„Ç¢„ÇØ„Çª„Çπ„ÅåÊãíÂê¶„Åï„Çå„Åü„Åã„ÄÅÂà©Áî®„Åß„Åç„Åæ„Åõ„Çì„ÄÇ");
      }
    }

    enableCamera();
  }, []);

  return (
    <div className="p-6 text-center">
      <h1 className="text-2xl font-bold mb-4">„Ç´„É°„É©Á¢∫Ë™ç„Éö„Éº„Ç∏</h1>
      <video
        ref={videoRef}
        autoPlay
        playsInline
        muted
        style={{ width: "100%", height: "auto", backgroundColor: "black" }}
      />
    </div>
  );
}
